.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transform: translateX(-50%)translateY(-50%)scale(0)rotate(0)skew(40deg, 40deg);
}

.is-hidden .our-location-modal-body {
